import fetch from 'isomorphic-unfetch';
import MasterPage from '../components/Master';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio = {props.precioBitcoin}
                />
            </div>
            <div className="col-md-8">
                <h2>Noticias del Bitcoin</h2>
                <Noticias 
                    noticias = {props.noticiasBitcoin}
                />
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos Bitcoin</h2>
                <Eventos 
                    eventos = {props.eventosBitcoin}
                />
            </div>
        </div>
    </MasterPage>
);
Index.getInitialProps = async () => {
    const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=4cc943cb36ba4a2d848e33bdea48e441&language=es');
    const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Chile&token=FGC7IMC63D4FPNBP3KW2');

    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();
    const resEventos = await eventos.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticiasBitcoin: resNoticias.articles,
        eventosBitcoin: resEventos.events
    }
}

export default Index;